import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';
import { MatDialog } from '@angular/material/dialog';
import { DialogLeadComponent } from '../dialog-lead/dialog-lead.component';
import { DialogCompraComponent } from '../dialog-compra/dialog-compra.component';
import { ConhecaNossanaveComponent } from '../conheca-nossanave/conheca-nossanave.component';
import { SalvoComSucessoComponent } from '../salvo-com-sucesso/salvo-com-sucesso.component';

declare const firebase;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {
  email: string;
  planetas = [
    {
      id: 'liberdade',
      nome: 'Liberdade para voar',
      class: 'liberdade_1',
      descricao: `Que tal vivenciar uma nova experiência em uma gravidade fantástica fazendo coisas cotidianas como praticar futebol, vôlei, tênis, lutas, além de, tomar banho, lavar louça e comer em outra gravidade? Se sim, não há dúvidas que essas experiências foram feitas para a sua liberdade de voar.`,
      nomeBotao: 'Voe'
    },
    {
      id: 'explore',
      nome: 'Explore o desconhecido',
      class: 'explore_1',
      descricao: `Já pensou em ser um explorador dos planetas do Circuto Kipuer e investigar as cavernas subterrâneas, os vulcões, a construção do paredão de gelo, além de ter contato com rochas, espécies, luas, anéis? Tudo isso pode ser desbravado por você. O desconhecido não é mais um problema.`,
      nomeBotao: 'Explore'
    },
    {
      id: 'aventure',
      nome: 'Aventure-se na fronteira',
      class: 'aventure_1',
      descricao: `As experiências de esportes radicais foram feitas para ultrapassarem as fronteiras energéticas. Rapel e bungee jump nas geleiras, saltar de asa delta de estruturas montanhosas, balão em gravidade diferente e, uma experiência aterrorizante ao caçar extraterrestres são aventuras ideais para quem se sente radical.`,
      nomeBotao: 'Aventure-se'
    },
    {
      id: 'conecte',
      nome: 'O espaço em seu negócio',
      class: 'conecte_1',
      descricao: `Destacar-se dos concorrentes sempre foi uma difícil missão, mas o espaço tem experiências essenciais para indústrias e empreendimentos. Que tal ter a oportunidade de ser o pioneiro a inovar com materiais almejados e não utilizados?  Na Via Espacial além de misturas de rocha, gelo, nitrogênio e outros materiais químicos exóticos, existe a liberação para livre pesquisa e utilização para a criação de novos produtos seja eles na área farmacêutica, cosmética, metalúrgica, tecnologa, construtora  e o que mais a sua mente permitir.`,
      nomeBotao: 'Conecte-se'
    },
  ]

  perguntas = [
    {
      pergunta: 'Como irei?',
      resposta: 'Desenvolvemos uma nave e com muito carinho a chamamos de ATENA, que irá te proporcionar uma agradável viagem na Via Espacial Trans-Netuniana.'
    },
    {
      pergunta: 'Preciso levar alimentos?',
      resposta: 'Não precisa se preocupar com isso, durante toda a experiência iremos fornecer todas as alimentações.'
    },
    {
      pergunta: 'Como vou respirar?',
      resposta: 'Na nossa querida Atena existe todo um sistema que distribui e armazena o oxigênio. Caso queira passear pelos planetas terá uma roupa adequada para que você não sofra nem um dano.'
    },
    {
      pergunta: 'Poderemos sentir algum impacto da pressão atmosférica?',
      resposta: 'Sim, mas calma serão similares a turbulência no avião e não afetaram em nada a nossa viagem.'
    },
    {
      pergunta: 'Poderei beber água tranquilamente ou terá alguma regra?',
      resposta: 'Não teremos nenhuma regra que controle a quantidade de água por passageiro, estaremos preparados para atender da melhor forma todos os nossos passageiros.'
    },
    {
      pergunta: 'A energia funcionará normalmente?',
      resposta: 'Sim, para chegar no destino almejado em alguns momentos passaremos por lugares de pouco luz mas, isso não é um problema pois terá luz e todo continuará funcionando normalmente.'
    },
    {
      pergunta: 'Essa viagem poderá causar algum dano a minha saúde?',
      resposta: 'Poderá se o passageiro não seguir nossas instruções de segurança e tomar decisões sem consultar nenhum atende.'
    },
    {
      pergunta: 'Existe alguma restrição de público?',
      resposta: 'Nossa embarcação é restrita para gestantes, portadores de doenças cardíacas, arterial e crianças entre ( 0 a 12 anos ).'
    },
    {
      pergunta: 'Como acontecerá o tempo para quem viajará?',
      resposta: 'Normalmente, vamos ir a voltar no tempo combinado em Terra.'
    },
    {
      pergunta: 'Terei seguro de vida?',
      resposta: 'Sim, todos os nossos passageiros terão seguro de vida.'
    },
    {
      pergunta: 'Como será a movimentação entre os planetas?',
      resposta: 'Todo trajeto acontecerá na Atena mas, quando estivermos no planeta desejado todos terão a oportunidade de colocar suas devidas roupas espaciais com o intuito de usufruir da experiência. '
    },
  ]

  constructor(private router: Router, private db: AngularFirestore, private dialog: MatDialog) { }

  ngOnInit() {
  }

  onScrollToFinish() {
    document.querySelector('.foguete .fogo').classList.add('d-none');
  }

  goToTop() {
    document.querySelector('.foguete .fogo').classList.remove('d-none');
  }

  @HostListener('window:scroll', ['$event'])
  onScroll() {
    if (window.scrollY === 0) {
      document.querySelector('.foguete').classList.add('d-none');
    } else {
      document.querySelector('.foguete').classList.remove('d-none');
    }
  }

  queroComprar(planetaId) {
    const dialogRef = this.dialog.open(DialogCompraComponent, {
      data: {
        nome: '',
        email: '',
        incrementeSuaViagem: {
          esquiar: false,
          montanhasRochosas: false,
          noiteRomantica: false,
          mundosGelados: false
        },
        data: null
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      debugger;
      if (!result) return;
      this.db.collection('compras').add({ ...result, planetaId, dataInclusao: new Date() })
      this.dialog.open(SalvoComSucessoComponent);
    });
  }

  conhecaNossaNave() {
    this.dialog.open(ConhecaNossanaveComponent);
  }

  enviarLead() {
    if(!this.email) return;
    this.db.collection('leads').add({ email: this.email, dataInclusao: new Date() })
    this.email = '';
    const dialogRef = this.dialog.open(DialogLeadComponent, {
      data: { nome: '', email: '' }
    });
  }

}
