import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogData } from '../dialog-lead/dialog-lead.component';

export interface DialogDataCompra {
  nome: string;
  email: string;
  incrementeSuaViagem: {
    esquiar: boolean,
    montanhasRochosas: boolean;
    noiteRomantica: boolean;
    mundosGelados: boolean;
  },
  data: Date;
}

@Component({
  selector: 'app-dialog-compra',
  templateUrl: './dialog-compra.component.html',
  styleUrls: ['./dialog-compra.component.less']
})
export class DialogCompraComponent {

  public startDatePicker = new Date(2045, 0, 1);

  constructor(
    public dialogRef: MatDialogRef<DialogCompraComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }

  onNoClick(): void {
    this.dialogRef.close();
  }

}
